body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  margin: 0;
  padding: 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
#root {
  height: 100vh;
  background-color: whitesmoke;
}
.content {
  height: 80%;
  width: 100%;
  top: 0;
  left: 0;
  background-color: whitesmoke;
  position: relative;
}
.content .grid {
  float: left;
  height: 100%;
  position: relative;
}
#col1 {
  width: 30%;
}
#col2 {
  width: 70%;
}
#bins {
  height: 75%;
  width: 250px;
  top: 25%;
  float: right;
  position: relative;
  overflow: auto;
}
#bin {
  height: 70px;
  width: 70px;
  margin: 10px;
  border-radius: 7px;
  border: 0px;
  background-color: white;
  padding: 20px 20px;
  text-align: center;
  box-shadow: 10px 10px 8px lightgray;
  transition: 1s;
  display: flex;
  left: 50%;
  position: relative;
}
#bin:hover {
  background-color: lightgreen;
}
#bin:focus {
  background: lightcoral;
  border-radius: 70px;
  outline: none;
}
#bin:active {
  background: crimson;
  border-radius: 70px;
  outline: none;
}
#containers {
  width: 90%;
  height: 95%;
  top: 5%;
  left: 5%;
  display: inline-block;
  position: absolute;
  border-radius: 12px;
  box-shadow: 10px 10px 8px lightgray;
  background-color: white;
  overflow: auto;
}

.title {
  font-style: initial;
  font-family: fantasy;
  color: white;
  background-color: lightgreen;
  width: fit-content;
  position: relative;
  box-shadow: 8px 8px 8px lightgray;
  border-radius: 10px;
  font-size: 25px;
}

.piechart {
  display: inline-flex;
  position: relative;
}
.piechart div{
  position: relative;
}
#piechartStat{
  position: absolute;
  font-family: fantasy;
  top: 48%;
  left: 34%;
  z-index: 1
}

.trashes {
  display: inline-flex;
  width: 100%;
  height: 100px;
}
.trash {
  width: 33.333%;
  margin: 10px;
  background-color: white;
  border-radius: 10px;
  box-shadow: 10px 10px 5px lightgray;
  border: 0px;
  font-size: 45px;
  font-family: fantasy;
  color: lightcoral;
  transition: 1s;
}
.trash:hover {
  background: #20b2aa;
  outline: none;
  border-radius: 70px;
  width: 22%;
}
.trash:focus {
  outline: none;
}

.bar {
  position: relative;
  background-color: white;
  border-radius: 50px;
  width: 100%;
  height: 60px;
}
.bar ul {
  text-decoration: none;
  width: fit-content;
  margin-left: auto;
  margin-right: auto;
  position: relative;
}
.bar ul li {
  list-style-type: none;
  display: inline-flex;
  height: auto;
  position: relative;
  margin: 0 20px;
  height: 60px;
}
.bar ul li a {
  text-decoration: none;
  color: lightsteelblue;
  font-size: 14px;
  transition: 0.5s;
}
.bar ul li a div img {
  width: 30px;
  height: 30px;
  display: block;
  margin: 0 auto;
}
.bar ul li a:hover {
  color: lightgreen;
  font-size: 23px;
  font-family: fantasy;
}

/* --Scroll bar-- */
::-webkit-scrollbar {
  width: 7px;
  height: 7px;
}
::-webkit-scrollbar-track {
  background: #f1f1f1;
}
::-webkit-scrollbar-thumb {
  background: lightgreen;
}
::-webkit-scrollbar-thumb:hover {
  background: #03fc03;
}
